import * as d3 from 'd3';
//import { IndexPage } from '../pages/index'

const fruits = new Map([
    ["apples", '500'],
    ["bananas", '300'],
    ["oranges", '200']
  ]);



export const generateRandomValues = (value, length = 6) =>
d3.range(length).map((item, index) => ({  
    label: 'Label'+ index,  
    value: Math.floor(Math.random()*10) }
));